<template>
  <div class="profile-container">
    <div class="profile-card">
      <div class="profile-header">
        <div class="header-left">
          <el-button icon="el-icon-back" size="small" @click="goToHome" class="back-btn">返回主页</el-button>
          <h2>个人信息</h2>
        </div>
        <div class="header-right">
          <el-button type="primary" size="small" @click="startEditing" v-if="!isEditing">编辑</el-button>
          <div v-else>
            <el-button type="success" size="small" @click="saveProfile">保存</el-button>
            <el-button size="small" @click="cancelEditing">取消</el-button>
          </div>
        </div>
      </div>

      <div class="profile-avatar-section">
        <el-avatar :size="80" class="user-avatar">{{ userInitial }}</el-avatar>
        <el-upload
          v-if="isEditing"
          class="avatar-uploader"
          action="/api/upload-avatar"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload">
          <el-button size="small" type="primary">更换头像</el-button>
        </el-upload>
      </div>

      <el-divider></el-divider>

      <div class="profile-info">
        <div class="info-item">
          <span class="label">用户名</span>
          <span v-if="!isEditing" class="value">{{ userProfile.username }}</span>
          <el-input v-else v-model="editingProfile.username" size="small"></el-input>
        </div>

        <div class="info-item">
          <span class="label">电子邮箱</span>
          <span v-if="!isEditing" class="value">{{ userProfile.email }}</span>
          <el-input v-else v-model="editingProfile.email" size="small"></el-input>
        </div>

        <div class="info-item">
          <span class="label">手机号码</span>
          <span v-if="!isEditing" class="value">{{ userProfile.phone || '未设置' }}</span>
          <el-input v-else v-model="editingProfile.phone" size="small"></el-input>
        </div>

        <div class="info-item">
          <span class="label">所在部门</span>
          <span v-if="!isEditing" class="value">{{ userProfile.department }}</span>
          <el-input v-else v-model="editingProfile.department" size="small"></el-input>
        </div>

        <div class="info-item">
          <span class="label">职位</span>
          <span v-if="!isEditing" class="value">{{ userProfile.position }}</span>
          <el-input v-else v-model="editingProfile.position" size="small"></el-input>
        </div>

        <div class="info-item">
          <span class="label">加入时间</span>
          <span class="value">{{ formatDate(userProfile.joinDate) }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'ProfileView',
  data() {
    return {
      isEditing: false,
      userProfile: {
        username: '张三',
        email: 'zhangsan@example.com',
        phone: '13800138000',
        department: '研发部',
        position: '前端工程师',
        joinDate: new Date('2023-01-15')
      },
      editingProfile: {}
    };
  },
  computed: {
    ...mapGetters(['currentUser']),
    userInitial() {
      return this.userProfile.username ? this.userProfile.username.charAt(0) : '用';
    }
  },
  created() {
    // 这里应该从后端获取用户信息
    // this.fetchUserProfile();
  },
  methods: {
    goToHome() {
      this.$router.push('/');
    },
    startEditing() {
      this.editingProfile = { ...this.userProfile };
      this.isEditing = true;
    },
    cancelEditing() {
      this.isEditing = false;
    },
    saveProfile() {
      // 这里应该调用API保存用户信息
      this.userProfile = { ...this.editingProfile };
      this.isEditing = false;
      this.$message.success('个人信息已更新');
    },
    // eslint-disable-next-line no-unused-vars
    handleAvatarSuccess(_res, _file) {
      // 处理头像上传成功
      this.$message.success('头像上传成功');
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 或 PNG 格式!');
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!');
      }
      return isJPG && isLt2M;
    },
    formatDate(date) {
      if (!date) return '';
      const d = new Date(date);
      return `${d.getFullYear()}-${String(d.getMonth() + 1).padStart(2, '0')}-${String(d.getDate()).padStart(2, '0')}`;
    },
    fetchUserProfile() {
      // 从后端获取用户信息
      // this.$axios.get('/api/user/profile').then(response => {
      //   this.userProfile = response.data;
      // }).catch(error => {
      //   this.$message.error('获取用户信息失败');
      // });
    }
  }
};
</script>

<style scoped>
.profile-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 24px;
  min-height: calc(100vh - 60px);
  background-color: #f5f7fa;
}

.profile-card {
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,0.1);
  width: 100%;
  max-width: 600px;
  padding: 24px;
}

.profile-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.header-left {
  display: flex;
  align-items: center;
}

.header-left h2 {
  margin: 0 0 0 12px;
  color: #303133;
}

.back-btn {
  padding: 6px 10px;
}

.header-right {
  display: flex;
}

.profile-avatar-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 16px 0;
}

.user-avatar {
  background-color: #409eff;
  color: white;
  font-weight: bold;
  margin-bottom: 12px;
}

.info-item {
  display: flex;
  margin-bottom: 16px;
}

.label {
  flex-basis: 120px;
  color: #606266;
  font-weight: 500;
}

.value {
  flex: 1;
  color: #303133;
}

@media screen and (max-width: 767px) {
  .profile-container {
    padding: 16px;
  }
  
  .profile-card {
    padding: 16px;
  }
  
  .info-item {
    flex-direction: column;
  }
  
  .label {
    margin-bottom: 4px;
  }
}
</style> 