<template>
  <div class="home-container">
    <!-- 左侧历史对话列表 -->
    <div class="chat-sidebar" :class="{ active: sidebarActive }">
      <div class="sidebar-header">
        <span class="sidebar-title">历史对话</span>
        <el-tooltip content="创建新的对话" placement="top" effect="light">
          <el-button type="text" icon="el-icon-plus" @click="createNewChat" class="new-chat-btn">新对话</el-button>
        </el-tooltip>
      </div>
      <div class="chat-history-list">
        <div
          v-for="(chat, index) in chatHistoryList"
          :key="index"
          class="history-item"
          :class="{ active: currentChatIndex === index }"
          @click="switchChat(index)"
        >
          <div class="history-item-content">
            <i class="el-icon-chat-dot-round"></i>
            <span class="history-title">{{ chat.title }}</span>
          </div>
          <el-dropdown
            trigger="click"
            @command="(cmd) => handleChatAction(cmd, index)"
            @click.stop
          >
            <i class="el-icon-more"></i>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="rename">重命名</el-dropdown-item>
              <el-dropdown-item command="share">共享</el-dropdown-item>
              <el-dropdown-item command="delete">删除</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>

    <!-- 侧边栏遮罩层 - 仅在移动端显示 -->
    <div
      class="sidebar-overlay"
      v-if="isMobile && sidebarActive"
      @click="toggleSidebar"
    ></div>

    <!-- 主要内容区域 -->
    <div class="main-content">
      <!-- 顶部操作栏 -->
      <div class="top-bar">
        <div class="model-selector">
          <el-tooltip content="收起/展开侧边栏" placement="bottom" effect="light">
            <el-button
              icon="el-icon-s-fold"
              class="sidebar-toggle-btn"
              @click="toggleSidebar"
            ></el-button>
          </el-tooltip>
          <el-tooltip content="切换AI模型" placement="bottom" effect="light">
            <el-dropdown trigger="click" @command="handleModelChange">
              <span class="el-dropdown-link">
                <i :class="getModelIcon()"></i>
                {{ currentModel }}
                <i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="GPT-4o">
                  <i class="el-icon-chat-round model-icon"></i> GPT-4o
                </el-dropdown-item>
                <el-dropdown-item command="DeepSeek">
                  <i class="el-icon-cpu model-icon"></i> DeepSeek
                </el-dropdown-item>
                <el-dropdown-item command="qianwen">
                  <i class="el-icon-star-off model-icon"></i> qianwen
                </el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </el-tooltip>
        </div>
        <div class="user-actions">
          <el-tooltip content="分享聊天记录" placement="bottom" effect="light">
            <el-button type="text" icon="el-icon-share" class="share-btn" @click="shareHistory"></el-button>
          </el-tooltip>
          <el-tooltip content="关于茵小浪" placement="bottom" effect="light">
            <el-button type="text" icon="el-icon-question" class="about-btn" @click="showAboutInfo"></el-button>
          </el-tooltip>
          <el-tooltip content="个人信息与设置" placement="bottom" effect="light">
            <el-dropdown trigger="click" @command="handleCommand">
              <el-avatar size="small" class="user-avatar">
                {{ userInitial }}
              </el-avatar>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="profile">
                  <i class="el-icon-user"></i> 个人信息
                </el-dropdown-item>
                <el-dropdown-item v-if="isAdmin" command="admin">
                  <i class="el-icon-s-tools"></i> 管理面板
                </el-dropdown-item>
                <el-dropdown-item command="logout">
                  <i class="el-icon-switch-button"></i> 退出登录
                </el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </el-tooltip>
        </div>
      </div>

      <!-- 聊天记录区域 -->
      <div class="chat-history" ref="chatHistory">
        <div class="greeting-section" v-if="currentChat.messages.length === 0">
          <h1 class="greeting-title">Hi~ 我是茵小浪</h1>
          <p class="greeting-subtitle">
            你身边的智能助手，可以为你答疑解惑、尽情创作，快来点击下任一功能体验吧~
          </p>
        </div>

        <!-- 提示词区域 -->
        <div class="prompt-section" v-if="currentChat.messages.length === 0">
          <div class="prompt-title">你可以这样问</div>
          <div class="prompt-list">
            <div
              class="prompt-item"
              @click="selectPrompt('午睡20分钟为什么能重置大脑运行效率？')"
            >
              午睡20分钟为什么能重置大脑运行效率？
            </div>
            <div
              class="prompt-item"
              @click="selectPrompt('问歌性斯答能减肥的原理是什么？')"
            >
              问歌性斯答能减肥的原理是什么？
            </div>
            <div
              class="prompt-item"
              @click="
                selectPrompt('作为忙碌的成年人，如何重建生活和心理的秩序？')
              "
            >
              作为忙碌的成年人，如何重建生活和心理的秩序？
            </div>
          </div>
        </div>

        <!-- 历史聊天记录展示区域 -->
        <div class="chat-messages" v-if="currentChat.messages.length > 0">
          <div
            v-for="(message, index) in currentChat.messages"
            :key="index"
            :class="[
              'message',
              message.role === 'user' ? 'user-message' : 'assistant-message',
              message.isError ? 'error-message' : '',
              message.isLoading ? 'loading-message' : '',
              message.isStreaming ? 'streaming-message' : '',
            ]"
          >
            <div class="avatar-container">
              <div :class="['avatar', message.role === 'user' ? 'user-avatar' : 'ai-avatar']">
                <template v-if="message.role === 'user'">我</template>
                <template v-else>AI</template>
              </div>
            </div>
            <div class="message-content">
              <template v-if="message.isLoading && !message.content">
                <span class="loading-dots">
                  思考中<span class="dot">.</span><span class="dot">.</span><span class="dot">.</span>
                </span>
              </template>
              <template v-else-if="message.role === 'user'">
                {{ message.content }}
              </template>
              <template v-else>
                <!-- 使用v-html渲染Markdown -->
                <div class="markdown-body" v-html="parseMarkdown(message.content)" v-if="!isImageResponse(message.content)"></div>
                
                <!-- 图片处理组件 -->
                <div v-if="isImageResponse(message.content)" class="ai-generated-image">
                  <div class="image-container">
                    <img :src="getImageUrl(message.content)" :alt="'AI生成的图片'" />
                    
                    <div class="image-controls">
                      <el-button
                        type="text"
                        icon="el-icon-download"
                        class="download-btn"
                        @click="downloadImage(getImageUrl(message.content))"
                      ></el-button>
                    </div>
                  </div>
                  
                  <div class="image-feedback" v-if="!message.feedbackGiven">
                    <el-button
                      size="small"
                      icon="el-icon-thumb"
                      @click="provideFeedback(message, 'like')"
                    >有帮助</el-button>
                    <el-button
                      size="small"
                      icon="el-icon-close"
                      @click="provideFeedback(message, 'dislike')"
                    >需改进</el-button>
                  </div>
                  <div v-else class="feedback-thanks" :class="message.feedbackType">
                    <i :class="message.feedbackType === 'like' ? 'el-icon-check' : 'el-icon-info'"></i>
                    {{ message.feedbackType === 'like' ? '感谢您的反馈！' : '感谢您的反馈，我们会继续改进！' }}
                  </div>
                </div>
              </template>
            </div>
          </div>
        </div>
      </div>

      <!-- 底部输入区域 -->
      <div class="input-container">
        <div class="upload-tips" v-if="showUploadTips">
          <div class="upload-tips-content">
            <i class="el-icon-info-circle"></i>
            支持上传文件（最多50个，每个100MB）
            <br>支持pdf、doc、xlsx、ppt、txt、图片等
            <span class="close-tips" @click="showUploadTips = false">×</span>
          </div>
        </div>
        
        <div class="input-area">
          <el-input
            type="textarea"
            :rows="1"
            placeholder="输入你的问题，按回车发送"
            v-model="userInput"
            class="chat-input"
            resize="none"
            @keydown.enter.native.prevent="sendMessage"
          >
          </el-input>
          <div class="input-actions">
            <el-tooltip content="上传文件" placement="top" effect="light">
              <el-button 
                icon="el-icon-link" 
                circle 
                size="small"
                class="action-button"
                @click="handleFileUpload"
                @mouseenter="showUploadTips = true"
                @mouseleave="showUploadTips = false"
              ></el-button>
            </el-tooltip>
            <el-tooltip content="添加图片" placement="top" effect="light">
              <el-button
                icon="el-icon-picture-outline"
                circle
                size="small"
                class="action-button"
                @click="handleImageUpload"
              ></el-button>
            </el-tooltip>
            <el-tooltip content="语音输入" placement="top" effect="light">
              <el-button
                icon="el-icon-microphone"
                circle
                size="small"
                class="action-button"
                @click="startVoiceInput"
              ></el-button>
            </el-tooltip>
          </div>
          <el-tooltip content="发送消息" placement="top" effect="light">
            <el-button
              type="primary"
              icon="el-icon-s-promotion"
              class="send-button"
              :disabled="!userInput.trim()"
              @click="sendMessage"
            ></el-button>
          </el-tooltip>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import { marked } from 'marked';
import hljs from 'highlight.js';
import 'highlight.js/styles/github.css';

// 配置marked
marked.setOptions({
  highlight: function (code, lang) {
    const language = hljs.getLanguage(lang) ? lang : 'plaintext';
    return hljs.highlight(code, { language }).value;
  },
  langPrefix: 'hljs language-',
  gfm: true,
  breaks: true,
});

export default {
  name: "HomeView",
  data() {
    return {
      userInput: "",
      currentChatIndex: 0,
      sidebarActive: true, // 默认桌面端侧边栏为展开状态
      chatHistoryList: [],
      currentSessionId: null,
      isLoading: false,
      // 流式响应相关
      streamedResponses: new Map(),
      showUploadTips: false,
      // 存储完整文档内容
      fullDocumentContent: null,
    };
  },
  computed: {
    ...mapGetters([
      "currentModel",
      "availableModels",
      "currentUser",
      "isAdmin",
    ]),
    userInitial() {
      return this.currentUser
        ? this.currentUser.username.charAt(0).toUpperCase()
        : "?";
    },
    currentChat() {
      return (
        this.chatHistoryList[this.currentChatIndex] || {
          id: 0,
          title: "新对话",
          messages: [],
        }
      );
    },
    isMobile() {
      return window.innerWidth <= 767;
    },
  },
  methods: {
    ...mapActions(["setCurrentModel", "logout", "toggleAccountModal"]),
    
    // 检查用户输入是否只是一个文件名
    isJustFileName(input) {
      return input && 
             (input.endsWith('.txt') || 
              input.endsWith('.docx') || 
              input.endsWith('.doc')) && 
             !input.includes('\n');
    },
    
    // Markdown解析方法
    parseMarkdown(text) {
      if (!text) return '';
      
      // 清理多余问号和特殊字符
      text = this.cleanTextContent(text);
      
      try {
        return marked(text);
      } catch (error) {
        console.error('Markdown解析错误:', error);
        return text;
      }
    },
    
    // 前端层面的内容清理
    cleanTextContent(text) {
      if (!text) return '';
      
      // 替换连续的多个问号为单个问号
      text = text.replace(/\?{2,}/g, '?');
      
      // 移除末尾的问号
      text = text.replace(/\s*\?+\s*$/, '');
      
      // 去掉不必要的空格
      text = text.replace(/\s+([,.!?;:])/g, '$1');
      
      return text;
    },
    
    async sendMessage() {
      if (!this.userInput.trim()) return;
      
      // 如果是新建的对话且没有会话ID，先创建会话
      if (!this.currentChat.id || this.currentChat.id === 0) {
        await this.createNewChatSession();
      }

      // 保存用户输入
      let userMessage = this.userInput;
      
      // 如果有完整文档内容且用户输入是纯文件名，添加文档内容
      if (this.fullDocumentContent && this.isJustFileName(this.userInput)) {
        userMessage = `已解析文件: ${this.userInput}\n\n${this.fullDocumentContent}`;
        // 重置完整文档内容
        this.fullDocumentContent = null;
      }
      
      // 添加用户消息到聊天历史
      this.currentChat.messages.push({
        role: "user",
        content: userMessage,
      });
      
      // 清空输入框
      this.userInput = ""; 
      
      // 滚动到底部
      this.$nextTick(() => {
        const chatHistory = this.$refs.chatHistory;
        if (chatHistory) {
          chatHistory.scrollTop = chatHistory.scrollHeight;
        }
      });
      
      // 显示加载状态
      this.isLoading = true;
      
      // 添加临时的AI消息，显示加载中
      const tempMessageIndex = this.currentChat.messages.length;
      const messageId = Date.now().toString();
      this.currentChat.messages.push({
        id: messageId,
        role: "assistant",
        content: "思考中...",
        isLoading: true,
        isStreaming: true
      });
      
      // 初始化流式响应
      this.streamedResponses.set(messageId, '');
      
      try {
        // 准备请求数据
        const requestData = {
          sessionId: this.currentChat.id,
          message: userMessage,
          modelType: this.currentModel.toLowerCase()
        };
        
        // POST方式创建EventSource（使用fetch发送POST请求，然后再用EventSource接收响应）
        // 首先发送POST请求
        const response = await fetch('http://localhost:5000/api/chat/stream', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(requestData)
        });
        
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        
        // 创建一个新的EventSource来接收流式响应
        const eventSource = new EventSource(`http://localhost:5000/api/chat/stream/${this.currentChat.id}`);
        
        // 处理消息事件
        eventSource.addEventListener('message', (event) => {
          const chunk = event.data;
          
          // 检查是否是结束标记
          if (chunk === '[DONE]') {
            eventSource.close();
            // 流式响应结束，更新消息状态
            this.currentChat.messages[tempMessageIndex].isLoading = false;
            this.currentChat.messages[tempMessageIndex].isStreaming = false;
            
            // 最终清理整个消息内容
            const finalContent = this.cleanTextContent(this.currentChat.messages[tempMessageIndex].content);
            this.currentChat.messages[tempMessageIndex].content = finalContent;
            
            // 如果是新对话，更新标题
            if (this.currentChat.title === "新对话") {
              this.updateChatTitle(this.currentChatIndex);
            }
            
            this.isLoading = false;
            return;
          }
          
          // 清理当前数据块
          const cleanedChunk = chunk.replace(/\?{2,}/g, '?');
          
          // 累积响应内容
          const currentContent = this.streamedResponses.get(messageId) + cleanedChunk;
          this.streamedResponses.set(messageId, currentContent);
          
          // 更新消息内容
          this.currentChat.messages[tempMessageIndex].content = currentContent;
          
          // 滚动到底部
          this.$nextTick(() => {
            const chatHistory = this.$refs.chatHistory;
            if (chatHistory) {
              chatHistory.scrollTop = chatHistory.scrollHeight;
            }
          });
        });
        
        // 处理错误事件
        eventSource.addEventListener('error', (event) => {
          console.error('SSE错误:', event);
          eventSource.close();
          
          // 更新消息为错误状态
          this.currentChat.messages[tempMessageIndex].isLoading = false;
          this.currentChat.messages[tempMessageIndex].isStreaming = false;
          this.currentChat.messages[tempMessageIndex].isError = true;
          this.currentChat.messages[tempMessageIndex].content = "获取AI响应失败，请稍后重试";
          
          this.$message.error("获取AI响应失败，请稍后重试");
          this.isLoading = false;
        });
      } catch (error) {
        console.error('发送消息出错:', error);
        
        // 判断是否是超时错误
        const isTimeout = error.message && (
          error.message.includes('timeout') || 
          error.message.includes('exceeded')
        );
        
        // 替换临时消息为错误消息
        this.currentChat.messages[tempMessageIndex].isLoading = false;
        this.currentChat.messages[tempMessageIndex].isStreaming = false;
        this.currentChat.messages[tempMessageIndex].isError = true;
        this.currentChat.messages[tempMessageIndex].content = isTimeout ? 
          "请求超时，AI模型响应时间过长。您可以尝试重新发送或换个简短的问题。" :
          `发送消息失败: ${error.message || '服务器错误'}`;
        
        const errorMsg = isTimeout ? 
          "请求超时，AI模型响应时间过长" : 
          `发送消息出错: ${error.message || '服务器错误'}`;
          
        this.$message({
          type: "error",
          message: errorMsg,
          duration: 5000,
          showClose: true
        });
        
        this.isLoading = false;
      }
    },
    
    // 更新对话标题
    updateChatTitle(index) {
      const chat = this.chatHistoryList[index];
      if (chat && chat.messages.length > 0) {
        const firstUserMessage = chat.messages.find(msg => msg.role === 'user');
        if (firstUserMessage) {
          chat.title = firstUserMessage.content.length > 20 
            ? firstUserMessage.content.substring(0, 20) + '...' 
            : firstUserMessage.content;
        }
      }
    },
    
    async createNewChatSession() {
      try {
        // 调用后端API创建新会话
        const response = await this.$axios.post('/api/chat/session', null, {
          params: {
            modelType: this.currentModel.toLowerCase()
          }
        });
        
        if (response.data.code === 200) {
          // 创建新对话
          const newChat = {
            id: response.data.data.id,
            title: "新对话",
            messages: [],
            defaultModelType: response.data.data.defaultModelType
          };
          
          // 添加到对话列表
          this.chatHistoryList.unshift(newChat);
          this.currentChatIndex = 0;
          this.currentSessionId = newChat.id;
          
          return newChat;
        } else {
          this.$message.error(`创建对话失败: ${response.data.message}`);
        }
      } catch (error) {
        console.error('创建对话出错:', error);
        this.$message.error(`创建对话出错: ${error.message || '服务器错误'}`);
      }
    },
    
    async loadUserSessions() {
      try {
        const response = await this.$axios.get('/api/chat/sessions');
        
        if (response.data.code === 200) {
          // 转换会话数据格式
          const sessions = response.data.data || [];
          
          // 清空当前会话列表
          this.chatHistoryList = [];
          
          // 加载所有会话
          for (const session of sessions) {
            const messagesResponse = await this.$axios.get(`/api/chat/messages/${session.id}`);
            
            if (messagesResponse.data.code === 200) {
              // 转换消息格式
              const messages = messagesResponse.data.data.map(msg => ({
                role: msg.role,
                content: msg.content
              }));
              
              // 添加到会话列表
              this.chatHistoryList.push({
                id: session.id,
                title: session.title,
                messages: messages,
                defaultModelType: session.defaultModelType
              });
            }
          }
          
          // 如果有会话，选择第一个会话
          if (this.chatHistoryList.length > 0) {
            this.currentChatIndex = 0;
            this.currentSessionId = this.chatHistoryList[0].id;
          } else {
            // 如果没有会话，创建一个新会话
            await this.createNewChatSession();
          }
        } else {
          this.$message.error(`获取会话列表失败: ${response.data.message}`);
        }
      } catch (error) {
        console.error('获取会话列表出错:', error);
        this.$message.error(`获取会话列表出错: ${error.message || '服务器错误'}`);
      }
    },
    
    // 根据当前模型获取对应图标
    getModelIcon() {
      const model = this.currentModel.toLowerCase();
      if (model.includes('gpt')) {
        return 'el-icon-chat-round model-icon';
      } else if (model.includes('deepseek')) {
        return 'el-icon-cpu model-icon';
      } else if (model.includes('kimi')) {
        return 'el-icon-star-off model-icon';
      } else {
        return 'el-icon-cpu model-icon'; // 默认图标
      }
    },
    
    // 处理模型切换时的动画效果
    handleModelChange(modelName) {
      // 添加切换动画
      const dropdown = document.querySelector('.model-selector .el-dropdown');
      if (dropdown) {
        dropdown.classList.add('active');
        setTimeout(() => {
          dropdown.classList.remove('active');
        }, 500);
      }
      
      // 更新模型
      this.setCurrentModel(modelName);
      
      // 显示提示
      this.$message({
        message: `已切换至 ${modelName} 模型`,
        type: 'success',
        duration: 2000
      });
    },
    
    selectPrompt(prompt) {
      this.userInput = prompt;
    },
    
    handleCommand(command) {
      if (command === "logout") {
        this.$store.dispatch('logout');
        this.$router.push("/login");
      } else if (command === "admin") {
        this.$router.push("/admin");
      } else if (command === "profile") {
        this.$router.push("/profile");
      }
    },
    
    async switchChat(index) {
      this.currentChatIndex = index;
      this.currentSessionId = this.chatHistoryList[index].id;
      
      // 滚动到对话历史底部
      this.$nextTick(() => {
        const chatHistory = this.$refs.chatHistory;
        if (chatHistory && this.currentChat.messages.length > 0) {
          chatHistory.scrollTop = chatHistory.scrollHeight;
        }
      });

      // 移动端时，选择对话后自动关闭侧边栏
      if (this.isMobile) {
        this.sidebarActive = false;
      }
    },
    
    async createNewChat() {
      // 动画效果
      const newChatBtn = document.querySelector('.new-chat-btn');
      if (newChatBtn) {
        newChatBtn.classList.add('creating');
        setTimeout(() => {
          newChatBtn.classList.remove('creating');
        }, 500);
      }
      
      try {
        // 显示加载提示
        const loadingInstance = this.$loading({
          lock: true,
          text: '正在创建新对话...',
          spinner: 'el-icon-loading',
          background: 'rgba(255, 255, 255, 0.7)'
        });
        
        // 调用后端API创建新会话
        const response = await this.$axios.post('/api/chat/session', null, {
          params: {
            modelType: this.currentModel.toLowerCase()
          }
        });
        
        loadingInstance.close();
        
        if (response.data.code === 200) {
          // 创建新对话
          const newChat = {
            id: response.data.data.id,
            title: "新对话",
            messages: [],
            defaultModelType: response.data.data.defaultModelType
          };
          
          // 添加到对话列表
          this.chatHistoryList.unshift(newChat);
          this.currentChatIndex = 0;
          this.currentSessionId = newChat.id;
          
          // 成功提示
          this.$message({
            type: 'success',
            message: '新对话创建成功',
            duration: 1500
          });
          
          // 移动端时，创建新对话后自动关闭侧边栏
          if (this.isMobile) {
            this.sidebarActive = false;
          }
          
          return newChat;
        } else {
          this.$message.error(`创建对话失败: ${response.data.message}`);
        }
      } catch (error) {
        console.error('创建对话出错:', error);
        this.$message.error(`创建对话出错: ${error.message || '服务器错误'}`);
      }
    },
    
    async handleChatAction(command, index) {
      if (command === "delete") {
        this.$confirm("确定要删除此对话吗?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        })
          .then(async () => {
            try {
              // 调用后端API删除会话
              const sessionId = this.chatHistoryList[index].id;
              const response = await this.$axios.delete(`/api/chat/session/${sessionId}`);
              
              if (response.data.code === 200) {
                // 删除本地会话
                this.chatHistoryList.splice(index, 1);
                
                // 如果没有会话了，创建一个新会话
                if (this.chatHistoryList.length === 0) {
                  await this.createNewChatSession();
                } else if (this.currentChatIndex >= index) {
                  // 如果删除的是当前会话或之前的会话，需要调整当前索引
                  this.currentChatIndex = Math.max(0, this.currentChatIndex - 1);
                  this.currentSessionId = this.chatHistoryList[this.currentChatIndex].id;
                }
                
                this.$message.success('删除对话成功');
              } else {
                this.$message.error(`删除对话失败: ${response.data.message}`);
              }
            } catch (error) {
              console.error('删除对话出错:', error);
              this.$message.error(`删除对话出错: ${error.message || '服务器错误'}`);
            }
          })
          .catch(() => {});
      } else if (command === "rename") {
        this.$prompt("请输入新的对话标题", "重命名", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          inputValue: this.chatHistoryList[index].title,
          inputValidator: (value) => {
            return value.trim().length > 0 || "标题不能为空";
          },
        })
          .then(async ({ value }) => {
            try {
              // 这里应该有一个更新标题的API，但后端代码没有提供
              // 更新本地会话标题
              this.chatHistoryList[index].title = value;
              this.$message.success('重命名成功');
            } catch (error) {
              console.error('重命名对话出错:', error);
              this.$message.error(`重命名对话出错: ${error.message || '服务器错误'}`);
            }
          })
          .catch(() => {});
      } else if (command === "share") {
        this.shareHistory();
      }
    },
    
    toggleSidebar() {
      this.sidebarActive = !this.sidebarActive;
      
      // 添加切换动画
      const toggleBtn = document.querySelector('.sidebar-toggle-btn');
      if (toggleBtn) {
        toggleBtn.classList.add('toggling');
        setTimeout(() => {
          toggleBtn.classList.remove('toggling');
        }, 500);
      }
      
      // 显示提示
      this.$message({
        message: this.sidebarActive ? '侧边栏已展开' : '侧边栏已收起',
        type: 'info',
        duration: 1500
      });
    },
    
    // 处理窗口大小变化
    handleResize() {
      const isMobileView = window.innerWidth <= 767;

      if (isMobileView) {
        // 移动端默认隐藏侧边栏
        this.sidebarActive = false;
      } else {
        // 桌面端默认显示侧边栏
        this.sidebarActive = true;
      }
    },
    
    // 共享历史记录
    shareHistory() {
      const shareLink = `http://localhost:5000/share/${this.currentChat.id}`;
      this.$alert(`共享链接: ${shareLink}`, "共享历史记录", {
        confirmButtonText: "复制链接",
        callback: (action) => {
          if (action === "confirm") {
            navigator.clipboard.writeText(shareLink);
            this.$message({
              type: "success",
              message: "链接已复制到剪贴板",
            });
          }
        },
      });
    },
    
    // 处理文件上传
    handleFileUpload() {
      // 创建文件输入元素
      const fileInput = document.createElement('input');
      fileInput.type = 'file';
      fileInput.accept = '.pdf,.doc,.docx,.xlsx,.ppt,.pptx,.txt';
      fileInput.multiple = false; // 修改为单个文件上传，以便更好处理文档解析
      fileInput.style.display = 'none';
      
      // 监听文件选择事件
      fileInput.addEventListener('change', async (event) => {
        const files = event.target.files;
        if (files && files.length > 0) {
          const file = files[0];
          const maxSize = 100 * 1024 * 1024; // 100MB
          
          if (file.size > maxSize) {
            this.$message.warning(`文件"${file.name}"超过100MB限制`);
            document.body.removeChild(fileInput);
            return;
          }
          
          // 检查文件类型是否为支持的文档类型
          const fileExt = file.name.substring(file.name.lastIndexOf('.')).toLowerCase();
          if (!['.txt', '.docx', '.doc'].includes(fileExt)) {
            this.$message.warning(`当前仅支持解析.txt和.docx/.doc文件`);
            document.body.removeChild(fileInput);
            return;
          }
          
          try {
            // 显示上传中提示
            const loadingInstance = this.$loading({
              lock: true,
              text: '解析文档中...',
              spinner: 'el-icon-loading',
              background: 'rgba(255, 255, 255, 0.7)'
            });
            
            // 创建FormData对象
            const formData = new FormData();
            formData.append('file', file);
            
            // 发送请求到后端文档解析API
            const response = await this.$axios.post('/api/file/parse-document', formData, {
              headers: {
                'Content-Type': 'multipart/form-data'
              }
            });
            
            loadingInstance.close();
            
            if (response.data.code === 200) {
              const documentData = response.data.data;
              const documentContent = documentData.content;
              
              // 提示解析成功
              this.$message.success('文档解析成功');
              
              // 只在输入框中显示文件名
              this.userInput = documentData.filename;
              
              // 存储完整文档内容，以便发送给AI
              this.fullDocumentContent = documentContent;
              
              // 显示提示
              if (documentContent.length > 1000) {
                this.$message.info('文档解析成功，发送后AI将分析完整文档内容');
              }
            } else {
              this.$message.error(`文档解析失败: ${response.data.message}`);
            }
          } catch (error) {
            console.error('文档解析出错:', error);
            this.$message.error(`文档解析出错: ${error.message || '服务器错误'}`);
          }
        }
        
        // 移除元素
        document.body.removeChild(fileInput);
      });
      
      // 添加到文档并触发点击
      document.body.appendChild(fileInput);
      fileInput.click();
    },
    
    // 处理图片上传
    handleImageUpload() {
      // 创建文件输入元素
      const fileInput = document.createElement('input');
      fileInput.type = 'file';
      fileInput.accept = 'image/*';
      fileInput.multiple = false; // 修改为单个图片
      fileInput.style.display = 'none';
      
      // 监听文件选择事件
      fileInput.addEventListener('change', async (event) => {
        const files = event.target.files;
        if (files && files.length > 0) {
          const file = files[0];
          const maxSize = 10 * 1024 * 1024; // 10MB
          
          if (file.size > maxSize) {
            this.$message.warning(`图片"${file.name}"超过10MB限制`);
            return;
          }
          
          try {
            // 显示上传中提示
            const loadingInstance = this.$loading({
              lock: true,
              text: '上传图片中...',
              spinner: 'el-icon-loading',
              background: 'rgba(255, 255, 255, 0.7)'
            });
            
            // 创建FormData对象
            const formData = new FormData();
            formData.append('file', file);
            
            // 发送请求到后端上传API
            const response = await this.$axios.post('/api/file/upload', formData, {
              headers: {
                'Content-Type': 'multipart/form-data'
              }
            });
            
            loadingInstance.close();
            
            if (response.data.code === 200) {
              // 上传成功，获取图片URL
              const imageUrl = response.data.data.url;
              
              // 将图片添加到输入框
              this.userInput += `\n分析这张图片:\n${imageUrl}`;
              
              this.$message.success('图片上传成功');
            } else {
              this.$message.error(`图片上传失败: ${response.data.message}`);
            }
          } catch (error) {
            this.$message.error(`图片上传出错: ${error.message || '服务器错误'}`);
          }
        }
        
        // 移除元素
        document.body.removeChild(fileInput);
      });
      
      // 添加到文档并触发点击
      document.body.appendChild(fileInput);
      fileInput.click();
    },
    
    // 开始语音输入
    startVoiceInput() {
      if (!('webkitSpeechRecognition' in window)) {
        this.$message.warning('您的浏览器不支持语音识别功能');
        return;
      }
      
      try {
        const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
        const recognition = new SpeechRecognition();
        
        recognition.lang = 'zh-CN';
        recognition.continuous = false;
        recognition.interimResults = true;
        
        let finalTranscript = '';
        
        // 显示语音输入状态
        this.$message({
          message: '正在聆听...',
          type: 'info',
          duration: 0,
          showClose: true,
          customClass: 'voice-message'
        });
        
        recognition.onresult = (event) => {
          let interimTranscript = '';
          
          for (let i = event.resultIndex; i < event.results.length; i++) {
            const transcript = event.results[i][0].transcript;
            
            if (event.results[i].isFinal) {
              finalTranscript += transcript;
            } else {
              interimTranscript += transcript;
            }
          }
          
          // 更新输入框
          this.userInput = (this.userInput || '') + finalTranscript + interimTranscript;
        };
        
        recognition.onerror = (event) => {
          console.error('语音识别错误:', event.error);
          this.$message.closeAll();
          this.$message.error('语音识别出错，请重试');
        };
        
        recognition.onend = () => {
          this.$message.closeAll();
          if (finalTranscript) {
            this.$message.success('语音输入完成');
          } else {
            this.$message.info('未检测到语音，请重试');
          }
        };
        
        recognition.start();
      } catch (error) {
        console.error('启动语音识别失败:', error);
        this.$message.error('启动语音识别失败，请检查浏览器权限设置');
      }
    },
    
    // 显示关于茵小浪的信息
    showAboutInfo() {
      this.$alert(
        `<div class="about-dialog">
          <h2>关于茵小浪</h2>
          <p>茵小浪是一个功能强大、界面友好的AI助手，专为解答问题、创建内容和辅助日常工作而设计。</p>
          <p>版本：1.0.0</p>
          <p>技术支持：support@yinxiaolang.com</p>
          <div class="about-features">
            <h3>主要功能</h3>
            <ul>
              <li>智能对话：基于先进的语言模型，提供准确的回答</li>
              <li>多模型支持：支持GPT-4o、DeepSeek、Kimi等多种AI模型</li>
              <li>文件解析：能够分析和处理多种格式的文件</li>
              <li>会话记忆：保持上下文连贯性，提供更自然的交流体验</li>
            </ul>
          </div>
        </div>`,
        '关于茵小浪',
        {
          dangerouslyUseHTMLString: true,
          confirmButtonText: '了解',
          customClass: 'about-dialog-container'
        }
      );
    },
    
    // 判断是否是图片响应
    isImageResponse(content) {
      try {
        const data = JSON.parse(content);
        return data && data.type === 'image' && data.url;
      } catch (e) {
        return false;
      }
    },
    
    // 从JSON中提取图片URL
    getImageUrl(content) {
      try {
        const data = JSON.parse(content);
        return data.url;
      } catch (e) {
        return '';
      }
    },
    
    // 下载图片
    downloadImage(url) {
      fetch(url)
        .then(response => response.blob())
        .then(blob => {
          const objUrl = window.URL.createObjectURL(blob);
          const link = document.createElement('a');
          link.href = objUrl;
          link.download = 'AI生成图片.jpg';
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
          window.URL.revokeObjectURL(objUrl);
        })
        .catch(error => {
          console.error('下载图片失败:', error);
          this.$message.error('下载图片失败，请重试');
          // 如果下载失败，尝试直接打开图片
          window.open(url, '_blank');
        });
    },
    
    // 提供反馈（点赞或差评）
    provideFeedback(message, type) {
      // 标记已提供反馈，防止重复操作
      this.$set(message, 'feedbackGiven', true);
      this.$set(message, 'feedbackType', type);
      
      // 这里可以添加向后端发送反馈的AJAX请求
      // 例如: this.$axios.post('/api/feedback', { messageId: message.id, type })
      
      // 显示反馈成功消息
      this.$message({
        type: 'success',
        message: type === 'like' ? '感谢您的反馈！' : '感谢您的反馈，我们会继续改进！',
        duration: 2000
      });
    },
  },
  created() {
    // 监听窗口大小变化
    window.addEventListener("resize", this.handleResize);

    // 初始化侧边栏状态
    this.handleResize();
    
    // 加载用户会话
    this.loadUserSessions();
    
    // 获取支持的模型类型
    this.$axios.get('/api/chat/models')
      .then(response => {
        if (response.data.code === 200) {
          // 这里可以更新可用模型列表
          console.log('支持的模型类型:', response.data.data);
        }
      })
      .catch(error => {
        console.error('获取模型类型出错:', error);
      });
  },
  beforeDestroy() {
    // 移除事件监听
    window.removeEventListener("resize", this.handleResize);
  },
};
</script>

